ks-listing-header {
    display: block;
    position: relative;
    z-index: 1;
    padding: 15px;

    box-shadow: var(--card-shadow);
    background-color: white;
    text-align: center;
    font-size: 0.875rem;
}

ks-listing-header .title {
    display: block;
    margin: 0 0 5px 0;
    
    font-family: var(--font-emphasis);
    font-weight: 700;
    font-size: 1.3rem;
    line-height: 1.3;
}

@media (max-width: 960px) {
    ks-listing-header .title {
        font-size: 1.105rem;
    }
}

ks-listing-header .autocorrect {
    font-size: 17px !important;
    font-weight: 500 !important;
    margin-bottom: 0px;
}

ks-listing-header .autocorrect > span {
    color: #ff3c3c;
    font-weight: 700;
}

ks-listing-header .description {
    max-width: 1200px;
    margin: 0 auto 15px auto;
    padding: 0 15px;

    line-height: 1.5;
}

ks-listing-header .tags {
    margin-top: 5px;
}

ks-listing-header hr {
    box-sizing: content-box;
    height: 0;
    overflow: visible;
    text-align: inherit;
    margin: 15px 0 15px 0;
    border: 0;
    border-top: 1px solid #e5e5e5;
}